@charset "UTF-8";
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-style: italic;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/poppins-mediumitalic.woff2") format("woff2"), url("../fonts/poppins-mediumitalic.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("../fonts/cormorantgaramond-regular.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%; }

.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  position: relative;
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 10px; }
  @media screen and (max-width: 1023.33px) {
    .container {
      max-width: 768px;
      padding: 0 44px; } }
  @media screen and (max-width: 767.66px) {
    .container {
      padding: 0 14px; } }

.container-slim {
  max-width: 880px;
  margin: 0 auto; }
  @media screen and (max-width: 1023.33px) {
    .container-slim {
      max-width: 100%;
      margin: 0; } }

.container-special-grid {
  width: 327px;
  margin-left: auto;
  margin-right: 0; }

.page,
.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 30px;
  color: #1d1613; }

.page__body {
  height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start; }

.overflow {
  overflow: hidden; }

.header {
  padding-top: 30px; }
  @media screen and (max-width: 767.66px) {
    .header {
      padding-top: 20px; } }

@media screen and (max-width: 1023.33px) {
  .header--open {
    background-color: #a87b62;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 1;
    overflow-y: scroll; } }

.header--open path {
  stroke: #ffffff; }

@media screen and (max-width: 1023.33px) {
  .header--open .logo {
    color: #ffffff; } }

.header__top {
  position: relative;
  display: flex;
  justify-content: space-between; }
  @media screen and (max-width: 1023.33px) {
    .header__top {
      justify-content: center; } }

.header__cart-area {
  margin-top: 2px; }

.header__cart-area-link {
  margin-left: 30px;
  font-weight: 500;
  text-decoration: none;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613; }
  @media screen and (max-width: 1023.33px) {
    .header__cart-area-link {
      display: none; } }

.header__cart-area-link--cart {
  display: none; }
  @media screen and (max-width: 1023.33px) {
    .header__cart-area-link--cart {
      display: block;
      position: absolute;
      right: 0;
      top: 25px; } }
  @media screen and (max-width: 767.66px) {
    .header__cart-area-link--cart {
      top: 15px; } }

.header__search {
  position: relative;
  margin: 0;
  padding: 0; }
  @media screen and (max-width: 1023.33px) {
    .header__search {
      display: none; } }

.header__search svg {
  position: absolute;
  left: 0;
  top: 15px; }

.header__search input {
  padding-left: 30px;
  border: none;
  border-radius: 20px; }

.header__search input::placeholder {
  letter-spacing: 0.7px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9B9997; }

.mask-background {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(86, 64, 52, 0.7); }

.menu {
  margin-top: 13px;
  border-top: 1px solid #F6F5F4; }
  @media screen and (max-width: 1023.33px) {
    .menu {
      border: none;
      display: none; } }

.menu__login {
  display: none; }

.menu__search {
  display: none; }

@media screen and (max-width: 1023.33px) {
  .menu--open,
  .menu--open .menu__login,
  .menu--open .menu__search,
  .menu--open .extra-navigation {
    display: block; } }

@media screen and (max-width: 1023.33px) {
  .menu__search {
    position: relative;
    margin: 0;
    margin-top: 35px;
    margin-bottom: 45px;
    padding: 0;
    width: 100%; } }

@media screen and (max-width: 767.66px) {
  .menu__search {
    margin-top: 25px;
    margin-bottom: 35px; } }

.menu__search svg {
  position: absolute;
  left: 12px;
  top: 15px;
  color: #ffffff;
  width: 16px;
  height: 16px; }

.menu__search input {
  padding-left: 30px;
  border: none;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  width: 100%;
  height: 40px;
  color: #ffffff; }

.menu__search input::placeholder {
  letter-spacing: 0.7px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: transparent; }

.menu__login {
  margin-top: 50px;
  padding-bottom: 80px;
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  text-decoration: none;
  color: #ffffff; }
  @media screen and (max-width: 767.66px) {
    .menu__login {
      margin-top: 40px; } }

.navigation {
  margin: 0;
  padding: 0;
  margin-top: 11px;
  margin-right: -40px;
  width: 100%; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.navigation__link {
  margin-right: 34px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  color: #1d1613; }

.navigation--open .navigation__list {
  display: block; }

.navigation--open .navigation__item {
  margin-bottom: 21px; }
  .navigation--open .navigation__item:last-child {
    margin-bottom: 35px; }
  @media screen and (max-width: 767.66px) {
    .navigation--open .navigation__item:last-child {
      margin-bottom: 25px; } }

.navigation--open .navigation__link {
  font-size: 24px;
  line-height: 36px;
  color: #ffffff; }

.extra-navigation {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none; }

.extra-navigation .extra-navigation__item {
  margin-bottom: 10px; }

.extra-navigation__link {
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.75);
  text-decoration: none; }

.name {
  position: relative;
  margin-top: 20px;
  padding-top: 195px;
  height: 463px;
  background-image: url("../img/background/main.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 2dppx) {
    .name {
      background-image: url("../img/background/main@2x.jpg"); } }
  @media screen and (max-width: 1023.33px) {
    .name {
      background-image: url("../img/background/main-tablet.jpg");
      background-position-x: center; } }
  @media screen and (max-width: 1023.33px) and (min-resolution: 2dppx) {
    .name {
      background-image: url("../img/background/main-tablet@2x.jpg"); } }
  @media screen and (max-width: 767.66px) {
    .name {
      background-image: url("../img/background/main-mobile.jpg"); } }
  @media screen and (max-width: 767.66px) and (min-resolution: 2dppx) {
    .name {
      background-image: url("../img/background/main-mobile@2x.jpg"); } }

.name h1,
.name p {
  margin: 0;
  padding: 0;
  text-align: center;
  color: #ffffff; }

.name h1 {
  font-family: "CormorantGaramond", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  letter-spacing: 0.03em;
  text-transform: uppercase; }

.name p {
  font-family: "CormorantGaramond", "Arial", sans-serif;
  font-style: italic;
  font-weight: 400;
  font-size: 38px;
  margin-top: 18px; }

.toggle {
  display: none;
  z-index: 5; }
  @media screen and (max-width: 1023.33px) {
    .toggle {
      position: absolute;
      left: -15px;
      top: 10px;
      display: block;
      width: 50px;
      height: 50px;
      border: none;
      background-color: transparent; } }
  @media screen and (max-width: 767.66px) {
    .toggle {
      top: 0; } }
  .toggle--off {
    display: none; }

.toggle span {
  display: block;
  position: absolute;
  top: 22px;
  left: 15px;
  width: 25px;
  height: 2px;
  background-color: #1d1613; }

.toggle span::before,
.toggle span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #1d1613;
  content: ""; }

.toggle span::before {
  top: -8px; }

.toggle span::after {
  bottom: -8px; }

/* состояние active при открытом меню  */
.toggle.is-active span,
.toggle.is-active span::before,
.toggle.is-active span::after {
  background-color: #ffffff; }

.footer {
  padding-bottom: 100px;
  background-color: #000; }

.footer__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.footer h2 {
  margin: 0;
  padding: 0;
  font-family: "CormorantGaramond", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  letter-spacing: 0.03em;
  color: #ffffff; }

.footer p {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.03em;
  color: #9b9997; }

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.footer__link {
  font-weight: 500;
  font-size: 16px;
  color: #ffffff;
  text-decoration: none; }

.logo {
  margin-left: -40px;
  color: #a87b62; }
  @media screen and (max-width: 1023.33px) {
    .logo {
      margin-left: 0; } }

@media screen and (max-width: 767.66px) {
  .logo svg {
    width: 52px;
    height: 52px; } }
